
.App {
  padding: 30px;
  display: flex;
  height: 100vh;
  background-color: #172136 ;
}

.App > div {
  margin-right: 50px;
}

.todolist {
  height: max-content;
  border: 3px solid #19193c;
  border-radius: 5px;
  padding: 20px;
  background-color: #9595c3 ;
}

.task {
  font-weight: bold;
}

.task-done {
  text-decoration: line-through;
  opacity: 0.5;
}

.active-filter {
  background-color: darkgreen;
  font-weight: bold;
  color: white;
  border-color: #19193c;
}

.filter-btn {
  background-color: darkseagreen;
  border-color: darkseagreen;
  color: white;
}


